<template>
	<div>{{ countdown | display(2) }}</div>
</template>

<script>
import { getUserAccount } from '@/api/Login'

export default {
	data() {
		return {
			now: NaN,
			time: 60 * 30, // 
		}
	},

	props: {
		submitPaper: {
			type: Function,
		},
		item: {
			type: Object,
		},
		questions: {
			type: Array,
		},
		scores: {
			type: Array,
		},
	},

	filters: {
		display(value, length) {
			const minute = ((value % 3.6e3) / 6e1) | 0,
				second = ((value % 6e1) / 1) | 0

			const format = (number) =>
				String.prototype.padStart.call(number, length, '0')

			return `${format(minute)}:${format(second)}`
		},
	},

	created() {
		const update = () => {
			this.now = (Date.now() / 1e3) | 0
			requestAnimationFrame(update)
		}
		update()
	},

	computed: {
		// 
		finishTime() {
			return ((Date.now() / 1e3) | 0) + this.time
		},
		//
		countdown() {
			return Math.max(0, this.finishTime - this.now)
		},
	},

	watch: {
		countdown(countdown) {
			if (countdown === 0) {
				this.submitPaper(this.item)
			}
		},
	},
}
</script>
