<template>
	<div class="about">
		<v-container>
			<!-- todo bug  /home/emsubjects -->
			<v-expansion-panels>
				<v-expansion-panel
					v-for="(subject,index) in subjects"
					:key="index"
				>
					<v-expansion-panel-header>
						<v-card flat>
							<v-row :class="`ma-0 subject ${subject.status}`">
								<v-col
									cols="12"
									md="6"
								>
									<h1 class="text-caption grey--text mb-2"></h1>
									<p>{{subject.course}}</p>
								</v-col>
								<v-col
									cols="6"
									sm="4"
									md="2"
								>
									<h1 class="text-caption grey--text mb-2"></h1>
									<p>{{subject.person}}</p>
								</v-col>
								<v-col
									cols="6"
									sm="4"
									md="2"
								>
									<h1 class="text-caption grey--text mb-2"></h1>
									<p>{{subject.due}}</p>
								</v-col>
								<v-col
									cols="2"
									sm="4"
									md="2"
								>
								</v-col>
							</v-row>
						</v-card>
					</v-expansion-panel-header>
					<v-expansion-panel-content>
						<v-card flat>
							<v-row>
								<v-col>
									<v-card-title>{{subject.course}}</v-card-title>
								</v-col>
								<v-col>
									<v-text-field
										v-model="subject.search"
										append-icon="mdi-magnify"
										label=""
										single-line
										hide-details
									></v-text-field>
								</v-col>
							</v-row>
							<v-data-table
								:headers="headers"
								:items="subject.papers"
								:search="subject.search"
							>
								<template v-slot:item.score="{item}">
									<v-chip color="success">{{scores[0]?Math.max(...scores):0}}</v-chip>
									<v-dialog
										v-model="item.dialog2"
										width="500"
										fullscreen
									>
										<template v-slot:activator="{ on, attrs }">
											<v-chip
												v-bind="attrs"
												v-on="on"
												color="error"
												style="cursor:pointer"
												class="ml-4"
											></v-chip>
										</template>
										<v-card
											max-width="400"
											tile
										>
											<v-toolbar
												dark
												color="primary"
											>
												<v-btn
													icon
													dark
													@click="item.dialog2 = false"
												>
													<v-icon>mdi-close</v-icon>
												</v-btn>
											</v-toolbar>
											<v-row style="height:130px"></v-row>
											<v-row>
												<v-col></v-col>
												<v-col>
													<v-subheader></v-subheader>
													<v-list-item
														v-for="(score,index) in scores"
														:key="index"
													>
														<v-list-item-content>
															<v-list-item-title>{{index+1}}{{score}}</v-list-item-title>
														</v-list-item-content>
													</v-list-item>
												</v-col>
												<v-col></v-col>
											</v-row>
										</v-card>

									</v-dialog>
								</template>

								<template v-slot:item.action="{ item }">
									<v-dialog
										v-model="item.dialog"
										fullscreen
										hide-overlay
										transition="dialog-bottom-transition"
									>
										<template v-slot:activator="{ on, attrs }">
											<v-btn
												v-bind="attrs"
												v-on="on"
												color="success"
												class="text-caption my-2 white--text "
												style="border-radius:5px"
												@click="answerPaper(item)"
												:disabled="answer"
											></v-btn>
										</template>
										<v-card>
											<v-toolbar
												dark
												color="primary"
											>
												<v-btn
													icon
													dark
													@click="item.dialog = false"
												>
													<v-icon>mdi-close</v-icon>
												</v-btn>
												<!-- S  -->
												<v-toolbar-title>
													<count-down
														:submitPaper="submitPaper"
														:item="item"
														:questions="questions"
														:scores="scores"
													/>
												</v-toolbar-title>
												<!-- E  -->
												<v-spacer></v-spacer>
												<v-toolbar-items>
													<v-btn
														dark
														text
														@click="submitPaper(item)"
													>
														
													</v-btn>
												</v-toolbar-items>
											</v-toolbar>
											<v-row class="content">
												<v-col cols="3"></v-col>
												<v-col>
													<v-radio-group
														v-for="(question,index) in questions"
														:key="question.qid"
														v-model="question.select"
													>
														<v-card-title>{{index+1}}. {{question.qcontent}}</v-card-title>
														<v-radio
															class="ml-2"
															v-for="option in question.qanswer.split(';')[0].split('&')"
															:key="option[0]"
															:label="option[0]+'. '+ option.split('=')[1]"
															:value="option.split('=')[1]"
														></v-radio>
													</v-radio-group>
												</v-col>
											</v-row>
										</v-card>
									</v-dialog>

									<v-dialog
										v-model="item.dialog1"
										fullscreen
										hide-overlay
										transition="dialog-bottom-transition"
									>
										<template v-slot:activator="{ on, attrs }">
											<v-btn
												v-bind="attrs"
												v-on="on"
												color="error"
												class="text-caption my-2 white--text mx-4"
												style="border-radius:5px"
												:disabled="answerAgain"
												@click="answerPaperAgain(item)"
											></v-btn>
										</template>
										<v-card>
											<v-toolbar
												dark
												color="primary"
											>
												<v-btn
													icon
													dark
													@click="item.dialog1 = false"
												>
													<v-icon>mdi-close</v-icon>
												</v-btn>
												<!-- S  -->
												<v-toolbar-title>
													<count-down
														:submitPaper="submitPaper"
														:item="item"
													/>
												</v-toolbar-title>
												<!-- E  -->
												<v-spacer></v-spacer>
												<v-toolbar-items>
													<v-btn
														dark
														text
														@click="submitPaper(item)"
													>
														
													</v-btn>
												</v-toolbar-items>
											</v-toolbar>
											<v-row class="content">
												<v-col cols="3"></v-col>
												<v-col>
													<v-radio-group
														v-for="(question,index) in questions"
														:key="question.qid"
														v-model="question.select"
													>
														<v-card-title>{{index+1}}. {{question.qcontent}}</v-card-title>
														<v-radio
															class="ml-2"
															v-for="option in question.qanswer.split(';')[0].split('&')"
															:key="option[0]"
															:label="option[0]+'. '+ option.split('=')[1]"
															:value="option.split('=')[1]"
														></v-radio>
													</v-radio-group>
												</v-col>
											</v-row>
										</v-card>
									</v-dialog>

									<v-dialog
										v-model="item.dialog3"
										fullscreen
										hide-overlay
										transition="dialog-bottom-transition"
									>
										<template v-slot:activator="{ on, attrs }">
											<v-btn
												v-bind="attrs"
												v-on="on"
												color="primary"
												class="text-caption my-2 white--text"
												style="border-radius:5px"
											></v-btn>
										</template>
										<v-card>
											<v-row>
												<v-toolbar
													dark
													color="primary"
												>
													<v-btn
														icon
														dark
														@click="item.dialog3 = false"
													>
														<v-icon>mdi-close</v-icon>
													</v-btn>
												</v-toolbar>
											</v-row>
											<v-row class="content">
												<v-col cols="3"></v-col>
												<v-col>
													<v-radio-group
														v-for="(question,index) in questions"
														:key="question.qid"
														v-model="question.select"
													>
														<v-card-title>{{index+1}}. {{question.qcontent}}</v-card-title>
														<v-radio
															class="ml-2"
															v-for="option in question.qanswer.split(';')[0].split('&')"
															:key="option[0]"
															:label="option[0]+'. '+ option.split('=')[1]"
															:value="option.split('=')[1]"
														></v-radio>
													</v-radio-group>
												</v-col>
											</v-row>
										</v-card>
									</v-dialog>
								</template>
							</v-data-table>
						</v-card>
					</v-expansion-panel-content>
				</v-expansion-panel>
			</v-expansion-panels>
		</v-container>
	</div>
</template>

<script>
import { getStudentInfo, getStudentClasses } from '@/api/Info'

import {
	getStudentAllPapers,
	getTime,
	getQuestions,
	getStudentAnswers,
} from '@/api/Paper'
import { getUserAccount } from '@/api/Login'

import CountDown from '../../Layouts/CountDown/CountDown.vue'

export default {
	name: 'About',
	components: {},
	data() {
		return {
			name: '', // 
			account: '', // 

			// 
			subjects: [],

			// 
			headers: [
				{
					text: '',
					align: 'start',
					sortable: false,
					value: 'pid',
				},
				{ text: '', value: 'pdescribe' },
				{ text: '', value: 'deadline' },
				{ text: '', value: 'action' },
				{ text: '', value: 'score' },
			],
			// JSON.parse(
			// 	'[{"qid":562,"qcontent":"1+1","point":"10","qanswer":"A=1&B=2&C=3&D=4;answer=2","pid":65,"qnum":1},{"qid":563,"qcontent":"1+1","point":"10","qanswer":"A=1&B=2&C=3&D=4;answer=2","pid":65,"qnum":2},{"qid":564,"qcontent":"1+1","point":"10","qanswer":"A=1&B=2&C=3&D=4;answer=2","pid":65,"qnum":3},{"qid":565,"qcontent":"1+1","point":"10","qanswer":"A=1&B=2&C=3&D=4;answer=2","pid":65,"qnum":4},{"qid":566,"qcontent":"1+2","point":"10","qanswer":"A=1&B=2&C=3&D=4;answer=2","pid":65,"qnum":5},{"qid":567,"qcontent":"1+2","point":"10","qanswer":"A=1&B=2&C=3&D=4;answer=2","pid":65,"qnum":6},{"qid":568,"qcontent":"1+2","point":"10","qanswer":"A=1&B=2&C=3&D=4;answer=2","pid":65,"qnum":7},{"qid":569,"qcontent":"1+2","point":"10","qanswer":"A=1&B=2&C=3&D=4;answer=2","pid":65,"qnum":8},{"qid":570,"qcontent":"1+2","point":"10","qanswer":"A=1&B=2&C=3&D=4;answer=2","pid":65,"qnum":9},{"qid":571,"qcontent":"1+2","point":"10","qanswer":"A=1&B=2&C=3&D=4;answer=2","pid":65,"qnum":10}]'
			// )
			questions: [], // 
			// 
			answer: false,
			answerAgain: true,
			scores: [], // 
		}
	},
	components: { CountDown },
	computed: {},
	created() {
		//
		this.questions.forEach((question) => {
			question['select'] = '' // 
			question['right'] = question.qanswer.split(';')[1].split('=')[1] // 
		})
		// 
		this.account = getUserAccount()

		getStudentInfo({
			snum: this.account,
		})
			.then((res) => {
				this.name = res.data.sname
				// 
				return getStudentClasses({
					snum: this.account,
				})
			})
			.then((res) => {
				// console.log(res)
				res.data.forEach((item) => {
					this.subjects.push({
						course: item.cname,
						id: item.cid,
						due: getTime(),
						person: this.name,
						status: 'ongoing',
						papers: [],
						search: '',
					})
				})
				// 
				this.subjects.forEach((subject) => {
					getStudentAllPapers({
						cid: subject.id,
						snum: this.account,
					}).then((res) => {
						if (!res.data.msg) {
							subject.papers = res.data.filter((paper) => paper)
							subject.papers.forEach((paper) => {
								paper['paperName'] = paper.pdescribe
							})
						}
					})
				})
			})
	},

	methods: {
		// 
		answerPaper(item) {
			this.scores = []
			this.answer = true
			this.answerAgain = !this.answer
			// id
			getQuestions({
				pid: item.pid,
			}).then((res) => {
				this.questions = res.data
				this.questions.forEach((question) => {
					question['select'] = '' // 
					question['right'] = question.qanswer.split(';')[1].split('=')[1] // 
				})
			})
		},
		answerPaperAgain(item) {
			this.questions.forEach((question) => {
				question.select = ''
			})
		},
		submitPaper(item) {
			let scoreNew = 0
			this.questions.forEach((question) => {
				scoreNew += question.point * Number(question.right === question.select)
			})
			if (item.hasOwnProperty('dialog1')) {
				item.dialog1 = false
			} else {
				item.dialog = false
			}
			this.$message({
				type: 'success',
				message: '',
			})
			this.scores.push(scoreNew)
		},
	},
}
</script>

<style scoped>
.ongoing {
	border-left: 4px solid #3cd1c2;
}
.subject.complete {
	border-left: 4px solid orange;
}
.subject.overdue {
	border-left: 4px solid tomato;
}
.v-chip.v-chip--no-color.ongoing {
	background-color: #3cd1c2;
}
.v-chip.v-chip--no-color.complete {
	background-color: orange;
}
.v-chip.v-chip--no-color.overdue {
	background-color: tomato;
}
header.v-sheet.theme--dark.v-toolbar.primary {
	position: fixed;
	width: 100%;
	z-index: 999;
}
.row.content {
	padding-top: 56px;
}
</style>
